import React, { useState, useEffect } from "react";
import "./Admin.scss";
import { Link } from "react-router-dom";
import { Button, TextField, TextareaAutosize } from "@mui/material";
import { ToastContainer, toast } from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';
import axios from "axios";



const url = window.location.origin;



const Adminpanel = () => {

  const params = new URLSearchParams(window.location.search).get("key");




  const [posts, setPosts] = useState([]);
  // const [ openPost, setOpenPost ] = useState({_id: null, caption: null, description: null, imageId: null})

  const updateAllData = (function () {
    axios({
      url: url + "/posts",
      method: 'GET',
      responseType: "json"
    })
      .then(data => {
        setPosts(data.data);
      })
      .catch(e => {
        console.log(e);
      })
  })

  useEffect(updateAllData, [])

  // function bazaUpdate() {
  //   "updateTheme" = posts._id,
  //   "updateTheme" = posts.caption
  //   "updateBody" = posts.description"

  const handleSumbit = (e) => {

    if (document.querySelector("input[type=\"file\"]").files.length == 0) {
      return toast("Post rasmi berilmadi", { type: "error" })
    }


    e.preventDefault()
    const formData = new FormData(document.getElementById('formOne'))
    axios({
      url: url + "/posts/add",
      method: "POST",
      data: formData,
      headers: { "Content-Type": "Application/JSON" }
    }).then((data) => {
      setPosts((post) => { return [...post, data.data] });
      toast("Yangi post qo'shildi", { type: "success" });
    })
      .catch((err) => {
        if (err.response) {
          return toast('Xatolik boldi ' + err.response.data.message, { type: "error" })
        }
        toast('Xato bor ' + err, { type: "error" });
      })
  }

  const editBtn = () => {
    const formData = new FormData(document.getElementById('formOne'))
    formData.append('_id', document.getElementById("_id").value)
    axios({
      url: url + "/posts/update",
      method: "PUT",
      data: formData,
      headers: { "Content-Type": "Application/JSON" }
    }).then((data) => {
      const updatablePostIndex = posts.findIndex(({ _id }) => { return _id == data.data._id });
      if (updatablePostIndex != -1) {
        setPosts((post) => {
          post[updatablePostIndex] = data.data;
          return [...post];
        });
        toast("Post Yangilandi", { type: "success" });
      } else {
        return toast("Bu post topilmadi!", { type: "error" });
      }


    })
      .catch((err) => {
        if (err.response) {
          return toast('Xatolik boldi ' + err.response.data.message, { type: 'error' });
        }
        toast('Xato bor ' + err, { type: "error" });
      })
  }


  const postOpen = (_id) => {
    axios({
      url: url + "/posts/getPost/" + _id,
      method: "GET",
      responseType: "json"
    }).then((data) => {
      // setOpenPost((post)=>{return data.data})
      document.getElementById('_id').value = data.data._id;
      document.getElementById('caption').value = data.data.caption;
      document.getElementById('description').value = data.data.description;
      document.getElementById('createdPostDate').value = data.data.createdPostDate;
      document.getElementById('postImage').src = url + "/posts/getImage/" + data.data.imageId
    })
      .catch((err) => {
        if (err.response) {
          return toast('Xatolik boldi ' + err.response.data.message, { type: "error" })
        }
        toast('Xato bor ' + err, { type: "error" })
      })

  }

  const deleteFunc = (_id) => {
    axios({
      url: url + "/posts/delete/",
      method: "delete",
      data: { _id: document.getElementById('_id').value },
      headers: { "Content-Type": "Application/JSON" }
    }).then((data) => {
      updateAllData();
      toast("Post o'chirildi", { type: "success" });
    })
      .catch(e => {
        if (e.response) {
          return toast("Xatolik bo'ldi" + e.response.message, { type: "error" });
        } else {

          return toast("Xatolik bo'ldi" + e, { type: "error" });

        }
      })
  }

  return (
    <>
      <div className="inner">
        <div className="card">

          <div className="btns">
            <Link to="/posts/admin" className="btn yozish">
              Post Yozish
            </Link>
          </div>

          <ul className="card__ul">
            {
              posts.map((user) => (
                <li variant="outlined" color="info" size="medium" key={user._id} onClick={() => postOpen(user._id)} >
                  <div className="user-img">
                    <img src={url + "/posts/getImage/" + user.imageId} onError={(e) => {
                      e.target.src = "https://www.pcfix.lt/wp-content/uploads/2019/10/default-user-image.png"
                    }} width='100' height='100' alt="no img" />
                  </div>
                  <div className="user-about">
                    <div className="description">
                      {user.description}
                    </div>
                    <div className="user-about__date">{user.createdPostDate}</div>
                  </div>
                </ li>
              ))
            }
          </ul>
        </div>

        <div className="form__card">
          <form id="formOne" onSubmit={handleSumbit} className="inner__form" encType="multipart/form-data" >
            <div className="inner__form__context">
              <div className="form__img-box">
                <img id="postImage" width='300' height='300' onError={(e) => {
                  e.target.src = "https://www.pcfix.lt/wp-content/uploads/2019/10/default-user-image.png"
                }} alt="" style={{position: "absolute"}} />

                <Button variant="contained" className="image-file-upload" onClick={(e) => {
                  e.target.children[0].click();
                }}>
                  Fayl yuklash
                  <input hidden={true} onChange={(e) => {
                    document.getElementById('postImage').src = (URL.createObjectURL(e.target.files[0]))
                  }} name='postImage' className="form__img__span" type="file" accept="image/jpeg" />
                </Button>


                {/* <span className="form__img__span">Rasm quyish</span> */}
              </div>

              <div className="form__section">

                <TextField className="form-inputs placeholder" id="_id" disabled={true}  variant="standard" />

              

                <div className="form-data">

                  
                  <div className="form-data__textarea" >
                  <TextField 
                      required
                      id="caption"
                      className="form-inputs addPending"
                      type="text"
                      name="caption"
                      placeholder="Post sarlavhasini kiriting"
                      autoComplete='off'
                      style={{backgroundColor: "#fff"}}
                  
                  />

                     {/* <input
                      required
                      id="caption"
                      className="form-inputs addPending"
                      type="text"
                      name="caption"
                      placeholder="Post sarlavhasini kiriting"
                      autoComplete='off'
                    />  */}
                  </div>

                  <div className='form-data__data' >
                    <TextField
                    className=" form-inputs addPending" type="date" required name="createdPostDate" id="createdPostDate" style={{backgroundColor: "#fff"}}
                    />
                    {/* <input className=" form-inputs addPending" type="date" required name="createdPostDate" id="createdPostDate" /> */}

                  </div>

                </div>

                <div className="form-textarea">

                    <TextareaAutosize
                    required
                    className="form-inputs"
                    id="description"
                    minRows={10}
                    cols={70}
                    placeholder="Post matnini kiriting"
                    name="description"
                    style={{outlineColor: "blue"}}
                    
                    />

                  {/* <textarea
                    className="form-inputs"
                    id="description"
                    cols="40"
                    rows="5"
                    placeholder="Post matnini kiriting"
                    name="description"
                  ></textarea> */}
                </div>
              </div>
            </div>
            <div className="gruop-btn">
              <Button
                className="inerbtn"
                type="button"
                onClick={handleSumbit}
                size="medium"
                variant="contained"
              >
                Postni ommaga chiqarish
              </Button>
              <Button
                className="inerbtn"
                type="button"
                size="medium"
                variant="contained"
                onClick={editBtn}
              >
                Postni Yangilash
              </Button>
              <Button onClick={deleteFunc}
                type="button"
                className="inerbtn"
                size="medium"
                variant="contained"
              >
                Postni o'chirish
              </Button>
            </div>
          </form>
          {/* <FetchAdmin /> */}
        </div>
      </div>

      <ToastContainer />
    </>
  );
};

export default Adminpanel;

